
import { computed } from 'vue';
import { useStore } from 'vuex';

export default {
  setup() {
    const store = useStore();

    return {
      logout: () => store.commit('logout'),
      loggedIn: computed(() => store.state.user.loggedIn),
      username: computed(
        () => `${store.state.user.name} ${store.state.user.surname}`,
      ),
    };
  },
};
